import { defineRouting } from 'next-intl/routing';
import { createNavigation } from 'next-intl/navigation';



export const NextIntlConfigurationWrapper = {
    LocationOfLocaleDecidingMiddleware: "./src/i18n/request.ts",
    localeRoutingConfiguration: defineRouting({
        locales: ['en', 'de'],
        defaultLocale: 'en',
    })
}


export const {Link, getPathname, redirect, usePathname, useRouter} = createNavigation(NextIntlConfigurationWrapper.localeRoutingConfiguration);
