import createMiddleware from 'next-intl/middleware';
import { NextIntlConfigurationWrapper } from './i18n/nextIntlConfigurationWrapper';

export default createMiddleware(NextIntlConfigurationWrapper.localeRoutingConfiguration);

export const config = {
    // Match all pathnames except for
    // - … if they start with `/api`, `/trpc`, `/_next` or `/_vercel`
    // - … the ones containing a dot (e.g. `favicon.ico`)
    matcher: '/((?!api|trpc|_next|_vercel|.*\\..*).*)'
    // matcher: '/((?!api|trpc|_next|_vercel|favicon\\.ico|.*\\..*).*)',

};
